<template>
	<div class="list-users">
		<div class="card">
			<div class="card-body">
				<h1>Lista de usuarios</h1>
				<hr>
				<div class="table-responsive">
					<table class="table table-striped">
						<thead>
							<tr>
								<th>Nombre</th>
								<th>Usuario</th>
								<th>Fecha Registro</th>
								<th>Opciones</th>
							</tr>
						</thead>
						<tbody>
							<tr v-if="users == 'error'">
								<td colspan="3" class="text-center">Sin registros</td>
							</tr>
							<tr v-if="processing.status && processing.type == 'loadUser'">
								<td colspan="4">Obteniedo registros ...</td>
							</tr>
							<tr v-for="(user,index) in users.data" :key="user.id">
								<td>{{ user.nombre +" "+user.apellidos }}</td>
								<td>{{ user.usuario }}</td>
								<td>{{ user.f_registro }}</td>
								<td>
									<div class="btn-group">
										<button class="btn btn-danger" data-toggle="modal" data-target="#mdlDeleteUSer" @click="deleteUser(user.user_id,index)">Del</button>
										<button class="btn btn-warning" data-toggle="modal" data-target="#mdEditUser" @click="loadDataModal(user,index)">Edit</button>
									</div>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
		<!-- Modal -->
		<div class="modal fade" id="mdEditUser" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLabel">Editar usuario</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						<div class="alert" v-if="statusTransaction" :class="{'alert-success':statusTransaction == 'success','alert-warning':statusTransaction == 'warning','alert-danger':statusTransaction == 'error'}" role="alert">
							{{messageRequest}}
						</div>
						<template v-if="processing.status && processing.type == 'updateUser'">
							<div class="procesing">
								<div class="progress">
									<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style="width: 100%"></div>								
								</div>
								<p class="text-center">Procesando datos...</p>
							</div>
						</template>
						<form>
							<div class="form-group">
								<label>Nombre:</label>
								<input type="text" name="" class="form-control" @change="change = true" v-model="userEdit.name">
							</div>
							<div class="form-group">
								<label>Appellidos:</label>
								<input type="text" name="" class="form-control" @change="change = true" v-model="userEdit.lastNames">
							</div>
							<div class="form-group">
								<label>Usuario:</label>
								<input type="text" name="" class="form-control" @change="change = true" v-model="userEdit.user">
							</div>							
						</form>
					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
						<button type="button" class="btn btn-primary" :disabled="!change || processing.status" @click="updateUser">Guardar</button>						
					</div>
				</div>
			</div>
		</div>
		<!-- End modal edit -->
		<!-- Modal confirm delete -->
		<div class="modal fade" id="mdlDeleteUSer" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog modal-sm" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLabel">Eliminar usuario</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						Esta seguro de eliminar el usuario?
					</div>
					<div class="modal-footer">
						<button type="button" class="btn btn-primary" data-dismiss="modal">Cerrar</button>
						<button type="button" class="btn btn-danger" @click="confirmDeleteUser">Eliminar</button>
					</div>
				</div>
			</div>
		</div>
		<!-- End modal confirm delete -->
	</div>	
</template>
<script>
	export default{
		name: 'List-users',		
		mounted(){
			this.getUsers();
		},
		data(){
			return {
				users: '',
				change: false,
				processing:{
					type: '',
					status: false
				},
				messageRequest: null,
				index: '',
				statusTransaction: null,
				idUser: null,
				userEdit:{
					id: '',
					name: '',
					lastNames: '',
					user: ''
				}
			}
		},
		methods:{
			getUsers(){
				this.processing.status = true;
				this.processing.type = 'loadUser';
				fetch('http://localhost/practicas/backend/backendvuejs/users/getusers')
				.then(response => response.json())
				.then((data)=>{
					this.users = data;
					this.processing.status = false;this.processing.type = '';
				})
				.catch((error)=>{
					console.log(error);
					this.processing.status = false;this.processing.type = '';
				})
			},
			loadDataModal(data,index){
				this.change = false;
				this.messageRequest = null;
				this.statusTransaction = null;
				this.index = index;
				this.userEdit.id = data.user_id;
				this.userEdit.name = data.nombre;
				this.userEdit.lastNames = data.apellidos;
				this.userEdit.user = data.usuario;
			},
			async updateUser(){
				let data = this.users.data[this.index],request,response,frmData = new FormData();
				frmData.append("data",JSON.stringify(this.userEdit));
				try {
					this.processing.status = true;this.processing.type = 'updateUser';
					request = await fetch("http://localhost/practicas/backend/backendvuejs/users/updateuser",{
						method: "POST",
						body: frmData
					});
					response =  await request.json();
					this.processing.status = false;this.processing.type = '';
					if (response.status == "success") {
						this.statusTransaction = "success";
						this.messageRequest = response.message;
						// load data view
						data.nombre = response.data.nombre;
						data.apellidos = response.data.apellidos;
						data.usuario = response.data.usuario;
						data.f_registro = response.data.f_registro;
					}else{
						this.statusTransaction = "warning";
						this.messageRequest = response.message;
						this.processing.status = false;this.processing.type = '';
					}
				} catch(e) {
					this.statusTransaction = "error";
					this.messageRequest = e;
					this.processing.status = false;this.processing.type = '';
					this.change = false;					
				}
				// console.log()
			},
			deleteUser(id,index){
				this.idUser = id;
				this.index = index;
			},
			async confirmDeleteUser(){
				let request,response,frmData = new FormData();
				frmData.append("dni",this.idUser);
				try {
					request = await fetch('http://localhost/practicas/backend/backendvuejs/users/removeuser',{
						method: 'POST',
						body: frmData
					});
					response = await request.json();
					if (response.status == "success") {
						alert(response.message);
						this.users.data.splice(this.index, 1);
					}else{
						alert(response.message);
					}
				} catch(e) {
					// statements
					console.log(e);
				}
			}
		}
	}
</script>