<template>
	<div class="login">
		<div class="container">
			<div class="row">
				<div class="col col-sm-6 col-md-5 col-lg-4">
					<div class="card">
						<div class="card-body">
							<form @submit.prevent="login">
								<h2>Iniciar sesion</h2>							
								<div class="form-group">									
									<div class="input-group">
										<div class="input-group-prepend">
											<span class="input-group-text"><i class="fas fa-user"></i></span>
										</div>
										<input type="text" name="" class="form-control" placeholder="test@gmail.com" v-model="dataUser.user">
									</div>
								</div>
								<div class="form-group">									
									<div class="input-group">
										<div class="input-group-prepend">
											<span class="input-group-text">#</span>
										</div>
										<input type="password" name="" class="form-control" placeholder="**********" v-model="dataUser.password">
									</div>
								</div>
								<div class="form-group">
									<button class="btn btn-success btn-block btn-lg">Iniciar sesion</button>
								</div>
								<p class="text-muted">Registrare ahora <router-link to="/register" href="#" class="text-success">Crear una cuenta</router-link></p>
								<div class="alert alert-danger" role="alert" v-if="this.error">{{this.error}}</div>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'Login',
		data(){
			return{
				dataUser:{
					user: '',
					password: ''
				},
				error: '',
				processing: false,
				response: ''
			}
		},
		methods:{
			async login(){				
				this.error = '';
				if (this.dataUser.user && this.dataUser.password) {
					this.error = '';
					let request,response,frmData = new FormData();
					frmData.append("data",JSON.stringify(this.dataUser));
					try {
						this.processing = true;
						request = await fetch('http://localhost/practicas/backend/backendvuejs/users/login',{
							method: 'POST',
							body: frmData
						});
						response = await request.json();
						if (response.found) {							
							let auth = {
								name: response.data.nombre,
								lastNames: response.data.apellidos,
								dniUsr: btoa(response.data.user_id),
								user: response.data.usuario
							}							
							localStorage.setItem("usrAuthuiep", JSON.stringify(auth));
							this.$router.push('/dashboard');
						}else{
							this.error = response.message;
						}
					} catch(e) {
						this.processing = false;
						console.log(e);
					}
					
				}else{
					this.error = 'Todos los campos son obligatorios';
				}				
			}
		}
	}
</script>
<style lang="scss">
	.login{
		display: flex;
		align-items: center;
		min-height: 100vh;
		.row{
			justify-content: center;
		}
	}
</style>