<template>
	<nav class="navbar navbar-expand-md navbar-light bg-light">
  <a class="navbar-brand" href="#">Navbar</a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item active">
        <router-link to="/dashboard" class="nav-link" href="#">Dashboard <span class="sr-only">(current)</span></router-link>
      </li>
      <li class="nav-item">
        <router-link to="/dashboard/users" class="nav-link" href="#">Usuarios</router-link>
      </li>
    </ul>
    <div>
      <div class="dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{ dataUser.name }}
        </a>
        <div class="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdown">
          <a class="dropdown-item" href="#">Ajustes</a>                  
          <a class="dropdown-item" href="#" @click.prevent="logOut">Cerrar sesion</a>
        </div>
      </div>
    </div>
  </div>
</nav>	
</template>
<script>
	export default{
		name: 'navigation',
    created(){
      this.dataUser = JSON.parse(localStorage.getItem("usrAuthuiep"));
      
    },
		data(){
			return {
        dataUser: ''
			}
		},
    methods:{
      logOut(){
        localStorage.removeItem("usrAuthuiep");
        this.$router.push("/login");
      }
    }
	}
</script>