import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../views/auth/Login'
import Register from '../views/auth/Register'
import Dashboard from '../views/Dashboard'
import Principal from '../views/Principal'
import ListUsers from '../views/users/ListUsers'
import EditUser from '../views/users/EditUser'

Vue.use(VueRouter)

const routes = [
{ path: '/', name:'main', redirect:{path:'/dashboard'} },
{ path: '/login', name:'login', component: Login },
{ path: '/register', name:'register', component: Register},
{ path: '/dashboard', name: 'dashboard', component: Dashboard,meta:{ requiresAuth: true },
  children:[
  { path: '', name:'Principal', component: Principal },
  { path: 'users', name: 'Lisa-usuarios', component: ListUsers },
  { path:'users/editar', name:'editar-usuario', component:EditUser }
  ]
}
]

const router = new VueRouter({
  routes,
  // mode: 'history'
  publicPath: './'
});
// verificar que existar inicio de sesion

router.beforeEach((to,from,next)=>{
  if (to.matched.some(record => record.meta.requiresAuth)) {
    let loggedIn = localStorage.getItem("usrAuthuiep");
    if (loggedIn) {
      next();
    }else{
      next({
        name:'login'
      })
    }
  }else{
    next();
  }  
})

export default router