<template>
	<div class="register">		
		<div class="container">
			<div class="row">
				<div class="col col-sm-6">
					<div class="card">
						<div class="card-body">
							<h1>Registro de usuario</h1>
							<hr>
							<form @submit.prevent="signUp">
								<div class="form-group">
									<label>Nombre:</label>
									<input type="text" name="" class="form-control" v-model="dataUser.name">
								</div>
								<div class="form-group">
									<label>Apellidos:</label>
									<input type="text" name="" class="form-control" v-model="dataUser.lastNames">
								</div>
								<div class="form-group">
									<label>Correo:</label>
									<input type="email" name="" class="form-control" v-model="dataUser.email">
								</div>
								<div class="form-group">
									<label>contraseña:</label>
									<input type="password" name="" class="form-control" v-model="dataUser.password">
								</div>
								<div class="form-group">
									<button class="btn btn-success btn-block" :disabled="processing">Registrar <span v-if="processing">Procesando ...</span></button>
								</div>								
								<div class="alert alert-danger alert-dismissible fade show" role="alert" v-if="error">
									{{ error }}
									<button type="button" class="close" data-dismiss="alert" aria-label="Close">
										<span aria-hidden="true">&times;</span>
									</button>
								</div>
								<p class="text-muted">Si ya tiene una cuenta pude <router-link to="/login" href="#" class="text-success">Iniciar sesion</router-link></p>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'Register',
		data(){
			return {
				dataUser:{
					name: '',
					lastNames: '',
					email: '',
					password: ''
				},
				processing: false,
				error: ''
			}
		},
		methods:{
			async signUp(){
				let request = null,response = null,frmData = new FormData();
				this.error = '';
				if (this.dataUser.name,this.dataUser.lastNames,this.dataUser.email,this.dataUser.password) {
					this.processing = true;
					try {
						frmData.append("data",JSON.stringify(this.dataUser));
						
						request = await fetch('http://localhost/practicas/backend/backendvuejs/users/adduser',{
							method: 'POST',
							body: frmData
						});
						this.processing = false;
						response = await request.json();
						if (response.status == "success") {
							this.dataUser.name = '',this.dataUser.lastNames = '',this.dataUser.email = '',this.dataUser.password = '';
							alert(response.message);
						}else{
							this.error = response.message;
						}
					} catch(e) {
						this.processing = false;
						this.error = e;						
					}
				}else{
					this.error = "Todos los campos son obligatorios";
				}
			}
		}
	}
</script>
<style lang="scss">
	.register{
		background-color: #ebedef;
		display: flex;
		align-items: center;
		min-height: 100vh;
		.row{
			justify-content: center;
		}
		.card{
			box-shadow: 0 1px 1px 0 rgba(60,75,100,.14), 0 2px 1px -1px rgba(60,75,100,.12), 0 1px 3px 0 rgba(60,75,100,.2);
		}
	}
</style>